<template>
  <!-- btn only shows if chrome detects app can be installed -->
  <button @click="installer()" :style="{ display: installBtn }">
    Install App
  </button>
</template>

<script>
export default {
  name: 'InstallBtn',
  props: {
    heading: String
  },
  data () {
    return {
      installBtn: 'none',
      installer: undefined
    };
  },
  created () {
    let installPrompt;

    window.addEventListener('beforeinstallprompt', e => {
      e.preventDefault();
      installPrompt = e;
      this.installBtn = 'block';
    });

    this.installer = () => {
      this.installBtn = 'none';
      installPrompt.prompt();

      installPrompt.userChoice.then(result => {
        if (result.outcome === 'accepted') {
          console.log('User accepted');
        } else {
          console.log('User denied');
        }
        installPrompt = null;
      });
    };
  }
};
</script>

<style lang="scss" scoped>
button {
  margin: 0 auto;
}
</style>
